<template>
    <div class="reset-container">
        <el-form autoComplete="on" label-position="left"
                 label-width="0px" :rules="rules" :model="signForm" ref="signForm"
                 class="card-box reset-form">
            <div>
                <router-link to="/credit-sign" class="back-icon">
                    首页<i class="el-icon-arrow-right"></i>业务信用分信用分<i class="el-icon-arrow-right"></i>签到<i class="el-icon-arrow-right"></i>添加
                </router-link>
            </div>
            <el-form-item prop="signScore">
                <span class="title">签到加分</span><el-input type="age" v-model.number="signForm.signScore" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="email">
                <span class="title">总分上限</span>
                <span class="choose">
                 <el-radio v-model="signForm.signLimit" label="2">无上限</el-radio>                          
                 </span>
                 <span class="choose">
                <el-radio v-model="signForm.signLimit" label="1">{{emptyctxt}}</el-radio><el-input name="email" type="number" :disabled="signForm.signLimit==2" v-model="signForm.signMaxInput" placeholder="单日上限总分" @input="validScore"></el-input>
                 </span>
            </el-form-item>
            <el-form-item>
                <span class="title">备注</span><el-input name="email" type="textarea" :autosize="{ minRows: 4}" placeholder="" v-model="signForm.memo"></el-input>
            </el-form-item>
            <!--<el-form-item prop="code">
                <span class="title">任务上限总分</span>
                <span class="choose">
                <el-radio v-model="missionMax" label="2">无上限</el-radio>
                </span>
                <span class="choose">
                <el-radio v-model="missionMax" label="1">{{emptyctxt}}</el-radio><el-input name="code" type="number" :disabled="missionMax==2" v-model="missionMaxInput" placeholder="任务上限总分" @input="validMission"></el-input>
                </span>
            </el-form-item>-->
            <el-form-item style="width:100%;">
                <el-button type="primary" style="width:80px" @click="updatedMax('signForm')">
                    添加
                </el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import {
        updateCreditSign
    } from 'api/basicCredit';

    export default {
        name: 'reset',
        data() {

            return {
                signForm: {
                    id: this.$route.query.id ? this.$route.query.id : "",
                    signScore: this.$route.query.score ? parseFloat(this.$route.query.score) : "",
                    signLimit: this.$route.query.totalScoreLimit == 0 ? "2" : "1",
                    signMaxInput: 0,
                    memo: this.$route.query.memo ? this.$route.query.memo : ""
                },
                rules: {
                    signScore: [{
                        required: true,
                        message: '请输入签到加分值'
                    }, {
                        type: 'number',
                        message: '加分值必须为数字值'
                    }, {
                        message: "最多输入小数点后两位",
                        pattern: /^\d+(\.\d{2})?$/,
                        trigger: "blur"
                    }]
                },
                emptyctxt: "",
                missionMax: '2',
                missionMaxInput: 0,
                signMaxInput: 0,
                signLimit: '2'
            }
        },
        created() {
            if (this.$route.query.totalScoreLimit) {
                this.signForm.signMaxInput = this.$route.query.totalScoreLimit == 0 ? 0 : this.$route.query.totalScoreLimit
            }
        },
        methods: {
            updatedMax(formName) {
                var _vue = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let ScoreMax = _vue.signForm.signLimit == 2 ? "-1" : _vue.signForm.signMaxInput;
                        let id = _vue.$route.query.id ? _vue.$route.query.id : "";
                        updateCreditSign(ScoreMax, _vue.signForm.signScore, _vue.signForm.memo).then(response => {
                            if (response.success) {
                                _vue.open();
                            } else {
                                _vue.open(response.data);
                            }
                            resolve();
                        }).catch(error => {});
                    } else {
                        return false;
                    }
                });

            },
            open(info) {
                var _vue = this;
                this.$alert(info ? info : '操作成功', '提示信息', {
                    confirmButtonText: '确定',
                    callback: action => {
                        _vue.$router.push('/credit-sign');
                    }
                });
            },
            validScore() {
                if (this.signForm.signMaxInput == "" || this.signForm.signMaxInput < 0) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.signForm.signMaxInput = 0;
                    });
                }
                if ((this.signForm.signMaxInput + "").length > 1 && (this.signForm.signMaxInput + "").startsWith("0") && (this.signForm.signMaxInput + "").indexOf(".") != 1) {
                    var _obj = this;
                    this.$nextTick(function() {
                        _obj.signForm.signMaxInput = 0;
                    });
                }
                var _obj = this;
                this.$nextTick(function() {
                    _obj.signForm.signMaxInput = _obj.toDecimal2(_obj.signForm.signMaxInput);
                });
            },
            togglePasswordType() {
                if (this.passwordType === 'text') {
                    this.passwordType = 'password'
                } else {
                    this.passwordType = 'text'
                }
            },
            toDecimal2(x) {
                var f = parseFloat(x);
                if (isNaN(f)) {
                    return false;
                }
                var f = Math.round(x * 100) / 100;
                var s = f.toString();
                var rs = s.indexOf('.');
                if (rs < 0) {
                    rs = s.length;
                    s += '.';
                }
                while (s.length <= rs + 2) {
                    s += '0';
                }
                return s;
            }
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";
    .reset-container {
        .el-form-item {
            float: left;
            width: 100%;
            margin-top: 20px;
            .title {
                float: left;
            }
            .el-input {
                display: inline;
                float: right;
                width: 60%;
            }
            .el-textarea {
                display: inline;
                float: right;
                width: 60%;
            }
            .choose {
                width: 60%;
                float: right;
                .el-radio {
                    float: left;
                }
                .el-input {
                    display: inline;
                    float: left;
                    width: 70%;
                    input {
                        float: left;
                        width: 100%;
                    }
                }
            }
        }
        input:-webkit-autofill {
            -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
            -webkit-text-fill-color: #3E3E3E !important;
        }
        @include relative;
        height: 100vh;
        background-color: #324057;
        .back-icon {
            float: left;
            margin-top: 5px;
        }
        .el-icon-information {
            position: absolute;
            right: -18px;
            top: 10px;
        }
        .reset-form {
            position: absolute;
            left: 0;
            right: 0;
            width: 350px;
            padding: 35px 35px 15px 35px;
            margin: 120px auto;
        }
        .card-box {
            padding: 20px;
            box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
            -webkit-border-radius: 5px;
            border-radius: 5px;
            -moz-border-radius: 5px;
            background-clip: padding-box;
            margin-bottom: 20px;
            background-color: #F9FAFC;
            width: 400px;
            border: 2px solid #8492A6;
        }
        .title {
            text-align: center;
            color: #505458;
        }
    }
</style>


